@mixin button {
	height: 50px;
	background: #007298;
	gap: 10px;
	color: white;
	font-size: 16px;
	font-weight: 700;
	line-height: 24px;
	letter-spacing: 0px;
	text-align: center;
	border: none;
	border-radius: 4px;
	text-transform: uppercase; }

* {
	margin: 0;
	font-family: 'Open Sans';
	box-sizing: border-box; }
header {
	height: 80px;
	background: #FFFFFF;
	box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.15);
	nav {
		padding: 15px 30px;
		display: flex;
		justify-content: space-between; } }
.name-logout {
	display: flex;
	justify-content: space-between;
	align-items: center;
	.name {
		font-size: 14px;
		font-weight: 300;
		line-height: 20px;
		letter-spacing: 0px;
		min-width: 85px;
		min-height: 20px; }
	.logout-button {
		@include button;
		width: 140px;
		padding: 13px 36px; } }
body {
	background: #F7F7F7; }

.container {
	width: 1122px;
	margin: 0 auto;
	padding-top: 74px;
	padding-bottom: 242px; }

.search-bar-add {
	display: flex;
	justify-content: space-between;
	.search-bar {
		width: 50%;
		display: flex;
		justify-content: space-between;
		&-input {
			width: 400px;
			height: 50px;
			border: 1px;
			border-color: #CFCFCF;
			border-radius: 4px;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			letter-spacing: 0px;
			text-align: left;
			padding: 13px 16px; }
		&-button {
			@include button;
			width: 148px;
			padding: 13px 43px; } }
	.add-button {
		@include button;
		width: 186px;
		padding: 13px 19px; } }
